<template>
  <div v-if="pageContent">
    <page :pageProp="pageContent"></page>
  </div>
</template>

<script>
import Page from "@/views/PageContent/Page.vue";

export default {
  name: "home",
  props: ["name"],
  components: {
    Page
  },
  computed: {
    pageContent() {
      return this.$store.state.pages.find(
        x =>
          x.post_title.toLowerCase().replace(/\s/g, "") ==
          this.componentName.toLowerCase()
      );
    },
    componentName() {
      return this.name == undefined
        ? "home"
        : this.name.toLowerCase().replace(/\s/g, "");
    }
  }
};
</script>

