
export default {
  methods: {
    extract_gp_blocks(rawBlocks) {
      return this.split(rawBlocks);
      // return this.formatBlocks(blocks, tertiaryColour);
    },
    split(raw) {
      //split on line breaks
      let arrayed = raw.split("\n");

      // split out the seperate block values into multiple sub arrays
      // they are surrounded by wp comment tags
      let spliced = [];
      let splicing = false;
      let splicedIndex = 0;
      let splicingElement = ""
      arrayed.forEach((element) => {
        if (element.includes("<!-- wp") && !splicing) {
          spliced[splicedIndex] = [];
          spliced[splicedIndex].push(element);
          splicing = true;
          splicingElement = element.split(" ")[1]
        } else if (splicing && element.includes("<!-- /wp") && element.includes(splicingElement)) { 
          splicing = false;
          spliced[splicedIndex].push(element)
          splicedIndex++;
        } else if (splicing) {
          spliced[splicedIndex].push(element);
        }
      });
    
      // join each block as single string
      let blocks = [];
      let blocksIndex = 0;
      spliced.forEach((element) => {
        let value = element.join("")

        blocks[blocksIndex] = {
          type: this.blockType(value),
          value: value,
        };

        blocksIndex++;
      });
      
      return blocks;
    },
    blockType(blockvalue) {
      if (blockvalue.includes("<!-- wp:paragraph")) {
        return "formatblock";
      } else if (blockvalue.includes("<!-- wp:heading")) {
        return "formatblock";
      } else if (blockvalue.includes("<!-- wp:video")) {
        return "formatblock";
      } else if (blockvalue.includes("<!-- wp:list")) {
        return "formatblock";
      } else if (blockvalue.includes("<!-- wp:separator")) {
        return "formatblock";
      } else if (blockvalue.includes("<!-- wp:core-embed/youtube")) {
        return "youtubeblock";
      } else if (blockvalue.includes("<!-- wp:buttons")) {
        return "multibuttonsblock";
      } else if (blockvalue.includes("<!-- wp:button") && !blockvalue.includes('rel="@')) {
        return "buttonblock";
      } else if (blockvalue.includes("<!-- wp:image")) {
        return "formatblock"//return "imageblock";
      } else if (blockvalue.includes("<!-- wp:embed")) {
        return "youtubeblock";
      }
    },
    extract_button_type(value) {  
      if (value.includes("<!-- wp:button") && !value.includes('rel="@')) {
        return "buttonblock";
      } else if (value.includes("<!-- wp:button") && value.includes('rel="@')) {
        return "buttonslidedownblock";
      } 
    },
    extract_button_blocks(value) {
      let arrayed = value.split("<!-- /wp:button -->");
      let buttons = arrayed.filter((x) => x.includes("<!-- wp:button"))

      let button_blocks = []

      buttons.forEach(b => {
        button_blocks.push({
          type: this.extract_button_type(b),
          value: b
        })
      })

      return {
        "buttonSettings": this.extract_buttons_settings(arrayed[0]),
        "buttons": button_blocks
      }
    },
    extract_buttons_settings(value) {
      if(value.includes("aligncenter")) {
        return "justify-content-center"
      } else if(value.includes("alignright")) {
        return "justify-content-end"
      } else {
        return "justify-content-start"
      }
    }
  },
};
