<template>
  <div v-if="buttonSettings != ''">
    <div class="row" :class="buttonSettings" v-if="buttons != []">
      <div class="col" v-for="(button, index) in buttons"
      :key="index">
        <buttonblock :value="button.value" :type="button.type"
        @slideOpenClicked=slideOpenClicked></buttonblock>
      </div>
    </div>  

    <transition :name="slideTranisitionStyle">
      <div class="row pt-3 mb-3 " v-if="slideOpen" 
      :style="'background-color: ' + slideDownBackgroundColor  + '; color: ' + slideDownColor  + ';'">
        <GutenbergDisplay :blocksRaw="postContent"></GutenbergDisplay>
      </div>
    </transition>

  </div>
</template>

<script>
import extract_button_blocks from "../gutenbergparser.js";
import buttonblock from "./ButtonBlock.vue"

export default {
  mixins: [extract_button_blocks],
  components: {
    buttonblock,
    GutenbergDisplay: () => import("@/components/gutenberg/GutenbergDisplay.vue")},
  data() {
    return {
      buttonSettings: "",
      buttons: [],
      slideOpen: false,
      postContent: "",
      slideTranisitionStyle: "",
      slideDownBackgroundColor: "",
      slideDownColor: ""

    };
  },
  props: {
    value: String
  },
  mounted() {
    let extracted = this.extract_button_blocks(this.value)
    this.buttonSettings = extracted["buttonSettings"]
    this.buttons = extracted["buttons"]
  },
  methods: {
    slideOpenClicked(title) {
      var post = this.$store.state.slide_to_posts.find(element => element.post_title === title);

      this.slideTranisitionStyle = post["acf"]["transition_style"]
      this.slideDownBackgroundColor = post["acf"]["background_colour"]
      this.slideDownColor = post["acf"]["color"]
      this.postContent = post["post_content"]
      this.slideOpen = !this.slideOpen
      
    }
  }

};
</script>

<style scoped>
.col {
  max-width: 40%;
}


.alignleft {
  text-align: left;
}

.alignright {
  float: right;
}

.aligncenter {
  text-align: center;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}

</style>
