import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import { remove } from 'lodash';
import ENUM from '@/enums/enum.js'
const api_root = process.env.VUE_APP_ROOT_API
const mode = process.env.VUE_APP_MODE

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    apiState: ENUM.INIT,
    pages: [],
    posts: [],
    slide_to_posts: [],
    primary_colour: '',
    secondary_colour: '',
    tertiary_colour: '',
    quarternary_colour: '',
    footer_area_colour: '',
    nav_area_bg: {},
    contact_area_background_image: {},
    settings_post_content: '',
    settings_post_title: ''
  },
  getters: {
    homePage: state => {
      return state.pages.find(x => x.post_title == 'Home')
    },
    sortedPosts: state=> {
      return state.posts.sort((a, b) => a.acf.order - b.acf.order)
    } 
  },
  mutations: {
    addSettings (state, value) {

      let settings = value.find(x => x.template == 'settingspage.php')


      state.settings_post_content = settings.post_content
      state.settings_post_title = settings.post_title

      state.primary_colour = settings.acf.primary_colour
      state.secondary_colour = settings.acf.secondary_colour
      state.tertiary_colour = settings.acf.tertiary_colour
      state.quarternary_colour = settings.acf.quarternary_colour
      state.footer_area_colour = settings.acf.footer_area_colour
      state.nav_area_bg = settings.acf.nav_area_bg
      state.contact_area_bannner_image = settings.acf.contact_area_bannner_image

      state.logo = settings.acf.logo

      state.apiState = ENUM.LOADED;
    },
    addPages (state, value) {
      state.pages = value;
    },
    addPosts (state, value) {
      var slide_tos = remove(value, function(x) {
        return x.categories.find(y => y.slug === "slide-down")
      });

      value.find(x => x.categories.find(y => y.cat_name === 'three-bar-banner'))
      
      state.slide_to_posts = slide_tos
      state.posts = value;
    },
  },
  actions: {
    async getSettings ({ commit }) {
      let data = {}
      await axios
        .get(api_root + mode + '/settings')
        .then(response => {
          data = response.data
          commit('addSettings', data)
        })
        .catch(error => {
          console.log(error)
        })
    },
    async getPages ({ commit }) {
      let data = {}
      await axios
        .get(api_root + mode + '/pages')
        .then(response => {
          data = response.data
          commit('addPages', data)
        })
        .catch(error => {
          console.log(error)
        })
    },
    async getPosts ({ commit }) {
      let data = {}
      await axios
        .get(api_root + mode + '/posts')
        .then(response => {
          data = response.data
          commit('addPosts', data)
        })
        .catch(error => {
          console.log(error)
        })
    },
  }
})
