<template>
  <div class="view intro hm-purple-light" :style="'background: url(' + bannerImage + ') no-repeat center center;'">
    <b-navbar toggleable="lg" type="dark" variant="info" class="bg-transparent">
      <b-navbar-brand href="#">
        <img id="nav-logo-img" :src="logoImage" alt="enginsoft-logo" />
        <span id="nav-logo-text">{{ siteTitle }}</span>
      </b-navbar-brand>

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav class="ml-auto">
          <b-nav-item v-if="this.$store.getters.homePage">
            <router-link to="/">
                <a class="show-on-mobile">
                   Home
                </a>
            </router-link>
          </b-nav-item>
          <span v-for="(page, index) in this.routes" :key="index">
            <div 
            >
              <b-nav-item v-if="page.acf.show_in_navbar">
                <router-link
                  :to="page.post_title.toLowerCase().replace(/\s/g, '')"
                >
                  <a class="show-on-mobile">
                      {{ page.post_title }}
                  </a>
                  
                </router-link>
              </b-nav-item>
            </div>
          </span>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
    <div class="container-fluid banner" id="top">
      <div class="container" id="text">
        <div class="col-md-5">

          <GutenbergDisplay :blocksRaw="postContent"></GutenbergDisplay>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
import GutenbergDisplay from "@/components/gutenberg/GutenbergDisplay.vue";
import _ from "underscore";


export default {
  name: "navbar",
  components: {
    GutenbergDisplay,
  },
  data() {
    return {
    };
  },
  computed: {
    routes() {
      let r = _.sortBy(this.$store.state.pages, "menu_order");
      return r.filter(
        (x) => x.post_title.toLowerCase().replace(/\s/g, "") != "home"
      );
    },
    bannerImage() {
      return this.$store.state.nav_area_bg
    },
    logoImage() {
      return this.$store.state.logo
    },
    postContent() {
      return this.$store.state.settings_post_content
    },
    siteTitle() {
      return this.$store.state.settings_post_title
    },
  },
  methods: {
  },
};
</script>

