<template>
  <div class="video-box text-center" style="min-width: 100%;">
    <div class="iframe-container">
      <iframe :src="this.source" frameborder="0" allowfullscreen>></iframe>
    </div>
    <em class="figureCaption" v-html="caption"></em>
  </div>
</template> 

<script>
var DomParser = require("dom-parser");
var parser = new DomParser();

export default {
  props: {
    value: String
  },
  data() {
    return {
      source: "",
      caption: ""
    };
  },
  mounted() {
    var dom = parser.parseFromString(this.value);
    let lowLevel = dom.getElementsByClassName("wp-block-embed__wrapper");

    this.caption = dom.getElementsByTagName("figcaption")[0]
      ? dom.getElementsByTagName("figcaption")[0].innerHTML
      : "";

    lowLevel.forEach(element => {

      this.source = element.innerHTML;
      //youtube video coming from the CMS is not embedded
      //need to extract the video id and embed the video
      const videoId = this.getYoutubeVideoId(element.innerHTML);

      this.source =
        "//www.youtube.com/embed/" +
        videoId +
        "?modestbranding=1&autohide=1&showinfo=0&controls=0&rel=0";
    });
  },
  methods: {
    getYoutubeVideoId(url) {

      if(url.includes("v=")) {
        var video_id = url.split("v=")[1];
        var ampersandPosition = video_id.indexOf("&");

        if (ampersandPosition != -1) {
          return video_id.substring(0, ampersandPosition);
        } else {
          return video_id;
        }
      }
      
      // if video is embedded then it will not contain v=
      if(!url.includes("v=")) {
        let index = url.indexOf("youtu.be/");

        if (index != -1) {
          return url.split("youtu.be/")[1];
        }
      }          
    }
  }
};
</script>

