<template>
  <div>
    <div class="align-self-end text-center">
      <a
        v-if="type=='buttonblock'"
        class="flat-butt text-center"
        :class="classes"
        :style="style"
        :href="href"
        :target="target"
      >
        <span style="padding-left: 10px;" v-html="text"></span>
      </a>
      <a
        v-if="type=='buttonslidedownblock'"
        class="flat-butt text-center"
        :class="classes"
        :style="style"
        @click="slideOpenClicked()"
      >
        <span style="padding-left: 10px;" v-html="text"></span>
      </a>
    </div>
  </div>
</template>

<script>
var DomParser = require("dom-parser");
var parser = new DomParser();

export default {
  data() {
    return {
      text: "",
      href: "#",
      style: "",
      classes: "",
      target: "",
      slideableTitle: ""
    };
  },
  props: {
    value: String,
    type: String
  },
  mounted() {
    var dom = parser.parseFromString(this.value);
    let topLevel = dom.getElementsByClassName("wp-block-button");

    topLevel.forEach(element => {
      element.attributes.forEach(atr => {
        if (atr.name === "class") {
          this.classes = atr.value;
        }
      });
    });

    let atag = dom.getElementsByTagName("a")[0]
      ? dom.getElementsByTagName("a")[0]
      : "";

    this.text = atag.innerHTML;

    if (atag.attributes) {
      atag.attributes.forEach(a => {
        if (a.name === "rel" && !a.value.includes("@")) {
          this.href = a.value.replace("noopener", "").replace("noreferrer", "");
          this.slideableTitle = ""
        }
        if (a.name === "rel" && a.value.includes("@")) {
          this.slideableTitle = a.value.replace("noopener", "").replace("noreferrer", "").replace("@", "");
        }
        if (a.name === "target") {
          this.target = a.value;
        }
        if (a.name === "style") {
          this.style = this.style + a.value;
        }
      });

    if (!this.style.includes("background-color")) {
      this.style =
        this.style +
        "; background-color: " +
        "#32373c" +
        ";";
    }

    if (!this.style.includes("border-radius")) {
      this.style =
        this.style +
        "; border-radius:26px;"
    }
  }
  },
  methods: {
    slideOpenClicked() {
      this.$emit("slideOpenClicked", this.slideableTitle)
    }
  }
};
</script>

<style>
.flat-butt {
  max-width: 560px;
}

a {
  text-decoration: none;
  margin-bottom: 20px;
}

.center-items {
  text-align: center;
  max-width: 560px;
}
</style>
