  <div>
    <div id="map" class="container-fluid">
      <div class="embed-responsive embed-responsive-21by9">
        <iframe
          frameborder="0"
          style="border:0"
          src="https://www.google.com/maps/embed/v1/place?q=The%20Venture%20Centre%20%20Sir%20William%20Lyons%20Rd%20%20Coventry%20%20CV4%207EZ&key=AIzaSyAa5TSKIY7nhJ0VZV8koYtxJeH8_Ia7Gi8"
          allowfullscreen
        ></iframe>
      </div>
    </div>

    <!-- <gmap-map
      :center="center"
